import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { pages } from './page';

import { PageContentComponent } from './page-content/page-content.component';

const routes: Routes  = [
  { path: '', redirectTo: '/home', pathMatch: 'full'},
  { path: 'home', component: PageContentComponent, data: { page: pages[0] }},
  { path: 'about', component: PageContentComponent, data: { page: pages[1] }},
  { path: 'projects', component: PageContentComponent, data: { page: pages[2] }},
  { path: 'resume', component: PageContentComponent, data: { page: pages[3] }}
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



